import React from 'react';

const CaseStudyTemplate = ({ pageContext }) => {
  const { study } = pageContext;
  return (
    <main>
      <h1>{study.name}</h1>
      <p>{study.description}</p>
      <img src={study.imagePath} alt={study.name} />
    </main>
  );
};

export default CaseStudyTemplate;
